import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Navigate, useLocation } from "react-router-dom";
import secureLocalStorage from "react-secure-storage";
import { setLogin } from "../feature/UserSlice";

const RequireAuth = ({ children }) => {
  const { data } = useSelector((state) => state.login);
  const location = useLocation();
  const dispatch = useDispatch();

  useEffect(() => {
    const dt = secureLocalStorage.getItem("data");
    const login = secureLocalStorage.getItem("login");

    if (data.length === 0) {
      if (dt !== null) {
        if ((dt.length !== null || dt.length !== 0) && login === true)
          dispatch(setLogin(dt));
      }
    }
  }, [data, data.length, dispatch]);

  if (Object.values(data).length < 1) {
    console.log("if");
    return <Navigate to="/" state={{ path: location.pathname }} />;
  } else {
    console.log("else");
    return children;
  }
};

export default RequireAuth;
